body {
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: 0;
  padding: 0;
  font-family: inherit;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  padding-top: 94px;
  padding-bottom: 94px;
}

.no-top-padding {
  padding-top: 0;
}

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container--flex {
  align-items: center;
  display: flex;
}

.form__modal {
  width: 450px;
  background-color: green;
  padding: 20px;
  position: relative;
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001a;
}

.close-btn {
  width: 30px;
  height: 30px;
  border: 1px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: box-shadow .4s cubic-bezier(.4, 0, .2, 1), background-color .4s cubic-bezier(.4, 0, .2, 1), border-width .4s cubic-bezier(.4, 0, .2, 1) .25s;
  display: flex;
  position: absolute;
  top: 6px;
  right: 6px;
}

.close-btn:hover, .close-btn:focus {
  background-color: #1a1919a7;
  border-width: 3px;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.form__wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.form__group {
  gap: 10px;
  margin-bottom: 10px;
}

.form__label {
  color: #0ff;
  margin-bottom: 4px;
  display: block;
}

.form__input {
  width: 356px;
  color: #8a2be2;
  background-color: #ff0;
  padding: 4px 20px;
  display: block;
}

.form__input::placeholder {
  color: #ff4500;
}

.form__radio-wrp {
  align-items: center;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.form__checkbox-wrp {
  flex-direction: column;
  align-items: flex-start;
  margin-right: 100px;
  display: flex;
}

.form__checkbox-label {
  padding: 4px;
  display: block;
}

.form__time {
  padding: 8px;
  display: block;
}

.form__commit {
  width: 410px;
  margin: 0 auto;
  display: block;
}

.form__commit-text {
  resize: none;
  padding: 10px;
}

.form__commit-text::placeholder {
  color: #6b8e23;
}

.btn {
  min-width: 30px;
  text-transform: uppercase;
  border: 1px solid #fff;
  border-radius: 6px;
  margin: 0 auto;
  padding: 6px 20px;
  display: block;
}

.form__submit-btn {
  text-align: center;
  color: orange;
  background-color: #faebd7;
  border-color: #00f;
  margin-bottom: 20px;
  transition: border-color .4s cubic-bezier(.4, 0, .2, 1), color .4s cubic-bezier(.175, .885, .32, 1.275) .1s, background-color .4s cubic-bezier(.4, 0, .2, 1), box-shadow .4s cubic-bezier(.175, .885, .32, 1.275) .1s;
  box-shadow: 2px 4px 2px #7fffd4;
}

.form__submit-btn:hover, .form__submit-btn:focus {
  box-shadow: 2px 5px 4px 0px 1px 3px #0000001f, 0px 1px 1px #00000024, 0px 2px 1px #0003;
  color: #fff;
  background-color: #212121;
  border-color: #ff4500;
}

.form__reset-btn {
  text-align: center;
  color: red;
  background-color: #faebd7;
  border-color: #00f;
  margin-bottom: 20px;
  transition: border-color .4s cubic-bezier(.4, 0, .2, 1), color .4s cubic-bezier(.175, .885, .32, 1.275) .1s, background-color .4s cubic-bezier(.4, 0, .2, 1), box-shadow .4s cubic-bezier(.175, .885, .32, 1.275) .1s;
  box-shadow: 2px 4px 2px #7fffd4;
}

.form__reset-btn:hover, .form__reset-btn:focus {
  color: #fff;
  background-color: #757575;
  border-color: #ff4500;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.backdrop {
  height: 100vh;
  width: 100vw;
  opacity: 1;
  transition: opacity 400ms cubic-bezier(.4, 0, .2, 1) visibility 400ms cubic-bezier(.4, 0, .2, 1);
  z-index: 9999;
  visibility: visible;
  background-color: #0003;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (max-width: 767.98px) {
  .backdrop {
    padding: 93px 15px;
    overflow-y: scroll;
  }
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.is-hidden .modal {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.1);
}

